import React from 'react'
//import ToDoItem from "./App-Parts/ToDoItem"
/** -- import components -- */

/**
 * JSX Header of the App
 */ 
function Header(props) {

    let i = []
    let clicked = (e) => {
        e.preventDefault()
        let inputVal = document.querySelector('#myInput')
        if (inputVal.value == '') {
            return;
        }
        i.push(inputVal.value)

        inputVal.value = ''
        props.parentCallback(i)
    }
    
    


    return (
    <>
        <div id="myDIV" className="header">
            <h2 style={{margin:'5px'}}>My To Do List</h2>
            <input type="text" id="myInput" placeholder="Title..." />
            <button id="addBtn" onClick={clicked} >Add</button>
        </div>
        <div className="container">
            <center>
                <h1>To-Do list</h1>
            </center>

        </div>
    </>
    )
}


export default Header