import React, { useState} from 'react'
import Header from './Header'
import ToDoItem from "./App-Parts/ToDoItem"

import "../css/App.css"

/**
 * The Core App function.
 * @param {*} props 
 */
function App(props) { 

    const [message, setMessage ] = useState([])

    let callbackFunction = (childData) => {
        setMessage([...message,childData])

    }
    let ToDoItems = message.map((msg, i) => <ToDoItem key={i} node_id={i} val={msg}/>)
    //console.log(message)

    return (
        <div className="root-container">
            <div className="header-container">
                <Header parentCallback={callbackFunction} />
            </div>
            <div className="main-content-container">
                <ul id="myUL">
                    {ToDoItems}
                </ul>
            </div>
        </div>
    )
}


export default App;
